.barnav {
  background: var(--second-color);
  height: 50px;
  padding: 10px 10px;
  display: flex;
  justify-content: space-between;
  position: sticky;
  top: 0;
  z-index: 400;
  margin-right: 5px;
}
.barnav img {
  height: 50px;
  border-radius: 90px;
}
.barnav .derecha {
  display: flex;
}
.barnav .derecha a {
  width: max-content;
  height: max-content;
  margin-left: 20px;
}
.barnav .derecha a i {
  font-size: 40px;
  color: var(--primary-color);
}
.barnav .derecha div {
  height: 50px;
  text-align: right;
}
.barnav .derecha div h2 {
  font-size: 25px;
  color: var(--font-color);
  margin: auto auto;
}
.barnav .derecha div h4 {
  color: var(--primary-color);
  font-size: 15px;
  margin: auto auto;
}
