/*///////////////*/
/* MODAL WINDOW */
/*/////////////*/
.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #111111bd;
  display: flex;
  pointer-events: none;
  transition: opacity 0.6s 0.9s;
  z-index: 500;
  opacity: 1;
  pointer-events: unset;
}

.modal__container {
  margin: auto;
  width: 90%;
  max-width: 600px;
  height: 95%;
  background-color: var(--second-color);
  border-radius: 6px;
  padding: 3em 2.5em;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.modal__title {
  font-size: 2.5rem;
}

.modal__paragraph {
  margin-bottom: 10px;
}

.modal__img {
  width: 90%;
  max-width: 300px;
}

.modal__close {
  position: relative;
  text-decoration: none;
  color: #fff;
  background-color: var(--primary-color);
  padding: 1em 3em;
  border: 1px solid;
  border-radius: 6px;
  display: inline-block;
  font-weight: 300;
  transition: background-color 0.7s;
  width: max-content;
}

.modal__close:hover {
  color: #f26250;
  background-color: #fff;
}
